.menu-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 469px;
  font-family: $font-poppins;
  font-weight: 400;
  font-size: 18px;
  font-weight: 32px;
}

.menu-list__item {
  border-bottom: 1px solid $color-grey;

}

.menu-list__title {
  font-size: 24px;
  line-height: 32px;
  font-weight: 600;
  margin: 20px 0 10px;
}

.menu-list__text {
  margin: 0 0 20px;
  padding: 0;
}

