.quotes {
  background-color: $color-purple;
  color: white;
  padding: 311px 0 220px;
}

.quotes__list {
  list-style-type: none;
  padding: 0;
  width: 785px;
  margin: 0 auto;
  font-family: $font-poppins;
}

.quotes__title {
  color: $color-gold;
  font-size: 36px;
  line-height: 40px;
  font-weight: 600;
  margin: 0 0 20px;
}

.quotes__text {
  font-size: 16px;
  line-height: 40px;
  text-align: center;
  margin: 0 0 40px;
}

.quotes__author {
  font-size: 24px;
  line-height: 40px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 6px;
  color: $color-gold;
}
