.header {
  font-family: $font-poppins;
  font-weight: 400;
  color: white;
  padding: 55px 0;
  background-color: grey;
}

.header__nav {
  display: flex;
  justify-content: space-between;
}

.header__link {
  text-decoration: none;
  color: white;
  align-self: center;

  &:hover {
    color: $color-gold;
  }
}
