.photos {
  padding-top: 68px;
  font-family: $font-poppins;
  color: white;
  background: linear-gradient($color-purple 50%, white 50% 100%);
}

.photos__title {
  font-size: 48px;
  line-height: 88px;
  margin: 0;
}

.photos__text {
  font-size: 16px;
  line-height: 32px;
  margin: 0 0 50px;
}

.photos__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}
