.promo {
  font-family: $font-poppins;
  font-weight: 500;
  color: white;
  background-color: grey;
  padding: 100px 0 265px;
}

.promo__hours {
  text-transform: uppercase;
  font-size: 24px;
  line-height: 32px;
  margin-bottom: 24px;
}

.promo__slogan {
  font-size: 64px;
  line-height: 88px;
  margin: 0 0 40px;
}

.promo__footer {
  display: flex;
  justify-content: space-between;

}
